import * as React from 'react';
import { Plugin } from '@ali/4ever-cangjie';
import QuickInsertTrigger from './components/quickInsertTrigger';
import { InsertGroup, InsertItem, QuickInsertPluginConfig } from './types';
import { getQuickInsertKeyword } from '@ali/4ever-utils';

function createRenderEditable(
  groups: InsertGroup[],
  insertMap: Record<string, InsertItem>,
  config: QuickInsertPluginConfig,
): Plugin['renderEditable'] {
  // @ts-ignore
  return function renderEditable(_, controller, next) {
    const isNecessary = controller.query('isSelectionInQuickInsert');
    if (!isNecessary) {
      return next();
    }
    const decoration = controller.query('getQuickInsertDecoration');
    const value = decoration?.mark.data.plugin;
    // 这里应该根据注册的插件传入type
    const text = getQuickInsertKeyword(controller, {type: 'mention'});
    return (
      <>
        {next()}
        <QuickInsertTrigger
          groups={groups}
          value={value}
          text={text}
          controller={controller}
          insertMap={insertMap}
          config={config}
        />
      </>
    );
  };
}

export default createRenderEditable;
